<template>
  <div class="row">
    <!-- fields column -->
    <div class="fields-ctn">
      <table v-for="keyField in Object.keys(fields)" :key="keyField">
        <thead>
          <tr>
            <th class="text-left text-uppercase gradient-th">{{keyField}}</th>
          </tr>
          <tr class="empty-row"></tr>
        </thead>
        <tbody>
          <tr v-for="(field, index) in fields[keyField]" :key="field.title">
            <td scope="row" class="text-left">{{ field.name }}</td>
          </tr>
        </tbody>
      </table>
    </div>
    <!-- alloys infos -->
    <div class="alloys-ctn">
      <div class="row">
        <!-- alloy column -->
        <div
          v-for="(alloy,index) in alloys"
          :key="index+ '_' + alloy.isoDesignation"
          class="alloy-col"
        >
          <!-- group table -->
          <table
            v-for="keyField in Object.keys(fields)"
            :key="keyField + '-' + alloy.isoDesignation"
          >
            <thead>
              <tr>
                <th></th>
              </tr>
              <tr class="empty-row"></tr>
            </thead>
            <tbody>
              <tr
                v-for="(field, index) in fields[keyField]"
                :key="alloy.isoDesignation + '-' + field.title"
              >
                <td
                  scope="row"
                  v-if="field.title == 'productUniverse'"
                >{{ alloy[field.title].split(' ', 3).join(' ') }}</td>
                <td scope="row" v-else-if="alloy[field.title]">{{ alloy[field.title] }}</td>
                <td scope="row" v-else></td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Fields from "@/config/fieldsDataSheet.js";

export default {
  name: "DataSheet",
  props: {
    alloys: {
      type: Array,
      default: null
    }
  },
  data: () => {
    return {
      fields: JSON.parse(JSON.stringify(Fields))
    };
  },
  watch: {
    alloys: function(oldAlloys, newAlloys) {
      return newAlloys;
    }
  }
};
</script>

